'use client';
import React, { useState } from 'react';
import { Home, BookOpen, Trophy, Calendar, Users, Settings } from 'lucide-react';

// Sidebar Navigation Component
const Sidebar = ({ activePage }: { activePage: string }) => {
  const menuItems = [
    { icon: Home, label: 'Dashboard', page: 'dashboard' },
    { icon: BookOpen, label: 'Learning Path', page: 'learning' },
    { icon: Trophy, label: 'Challenges', page: 'challenges' },
    { icon: Calendar, label: 'Events', page: 'events' },
    { icon: Users, label: 'Rooms', page: 'rooms' }
  ];

  return (
    <div className="w-64 bg-black p-6 flex flex-col border-r border-gray-800">
      <div className="text-orange-500 text-2xl font-bold mb-12">logo</div>
      
      <nav className="flex-1 space-y-2">
        {menuItems.map((item, index) => (
          <button
            key={index}
            className={`w-full flex items-center gap-3 px-4 py-3 rounded-lg transition-colors ${
              item.page === activePage 
                ? 'bg-orange-500/10 text-orange-500' 
                : 'text-gray-400 hover:text-white hover:bg-white/5'
            }`}
          >
            <item.icon size={20} />
            <span>{item.label}</span>
          </button>
        ))}
      </nav>

      <button className="w-full bg-orange-500 text-black font-semibold py-3 px-6 rounded-lg hover:bg-orange-600 transition-colors">
        Host a CTF
      </button>
    </div>
  );
};

const ChallengesPage = () => {
  const [challenges] = useState([
    {
      id: 1,
      title: "Web Exploitation",
      progress: 12,
      status: "in-progress"
    },
    {
      id: 2,
      title: "Cryptography",
      progress: 0,
      status: "not-started"
    },
    {
      id: 3,
      title: "Reverse Engineering",
      progress: 100,
      status: "completed"
    },
    {
      id: 4,
      title: "Forensics",
      progress: 0,
      status: "not-started"
    },
    {
      id: 5,
      title: "Binary Exploitation",
      progress: 0,
      status: "not-started"
    },
    {
      id: 6,
      title: "OSINT",
      progress: 0,
      status: "not-started"
    },
    {
      id: 7,
      title: "Misc",
      progress: 0,
      status: "not-started"
    },
    {
      id: 8,
      title: "Cloud Security",
      progress: 0,
      status: "not-started"
    },
    {
      id: 9,
      title: "AI / ML Exploitation",
      progress: 0,
      status: "not-started"
    }
  ]);

  const getButtonStyle = (status: string) => {
    if (status === "completed") {
      return "bg-orange-500 text-black hover:bg-orange-600";
    } else if (status === "in-progress") {
      return "border-2 border-orange-500 text-orange-500 bg-transparent hover:bg-orange-500/10";
    }
    return "bg-gray-300 text-black hover:bg-gray-400";
  };

  const getButtonText = (status: string) => {
    if (status === "completed") return "Challenge Completed";
    if (status === "in-progress") return "Continue Challenge";
    return "Start Challenge";
  };

  return (
    <div className="flex h-screen bg-black text-white font-sans">
      <Sidebar activePage="challenges" />

      {/* Main Content */}
      <div className="flex-1 p-8 overflow-y-auto">
        {/* Header */}
        <div className="flex justify-between items-center mb-8">
          <h1 className="text-4xl font-bold">Challenges</h1>
          <button className="w-12 h-12 rounded-full bg-orange-500 flex items-center justify-center hover:bg-orange-600 transition-colors">
            <Settings size={20} className="text-black" />
          </button>
        </div>

        {/* Challenges Grid */}
        <div className="grid grid-cols-3 gap-6">
          {challenges.map((challenge) => (
            <div 
              key={challenge.id}
              className="bg-[#0f0f0f] border border-gray-800 rounded-xl p-6 flex flex-col"
            >
              <h3 className="text-xl font-bold mb-4">{challenge.title}</h3>
              
              <div className="mb-4">
                <div className="w-full bg-gray-800 h-2 rounded-full overflow-hidden">
                  <div 
                    className="h-full bg-white rounded-full transition-all duration-300"
                    style={{ width: `${challenge.progress}%` }}
                  />
                </div>
                <div className="text-right text-sm text-gray-400 mt-1">{challenge.progress}%</div>
              </div>

              <button 
                className={`w-full py-3 rounded-lg font-semibold transition-all ${getButtonStyle(challenge.status)}`}
              >
                {getButtonText(challenge.status)}
              </button>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default ChallengesPage;